/*switch css*/
.button-switch{font-size:1rem;height:2.55rem;margin-bottom:0.625rem;position:relative;width:4.5rem;}
.button-switch .lbl-off,.button-switch .lbl-on{cursor:pointer;display:block;font-size:0.7rem;line-height:1rem;position:absolute;top:0.35rem;-webkit-transition:opacity 0.25s ease-out 0.1s;-o-transition:opacity 0.25s ease-out 0.1s;transition:opacity 0.25s ease-out 0.1s;text-transform:uppercase;}
.button-switch .lbl-off{right:1.5rem;}
.button-switch .lbl-on{color:#fefefe;opacity:0;left:0.4375rem;}
.button-switch .switch{-webkit-appearance:none;-moz-appearance:none;appearance:none;height:0;font-size:1rem;left:0;line-height:0;outline:none;position:absolute;top:0;width:0;}
.button-switch .switch:before,.button-switch .switch:after{content:'';font-size:1rem;position:absolute;}
.button-switch .switch:before{border-radius:1.25rem;background:#bdc3c7;height:1.875rem;left:-0.25rem;top:-0.1875rem;-webkit-transition:background-color 0.25s ease-out 0.1s;-o-transition:background-color 0.25s ease-out 0.1s;transition:background-color 0.25s ease-out 0.1s;width:4.5rem;}
.button-switch .switch:after{-webkit-box-shadow:0 .0625rem .375rem 0 #666;box-shadow:0 .0625rem .375rem 0 #666;border-radius:50%;background:#fefefe;height:1.5rem;-webkit-transform:translate(0,0);-ms-transform:translate(0,0);transform:translate(0,0);-webkit-transition:-webkit-transform 0.25s ease-out 0.1s;transition:-webkit-transform 0.25s ease-out 0.1s;-o-transition:transform 0.25s ease-out 0.1s;transition:transform 0.25s ease-out 0.1s;transition:transform 0.25s ease-out 0.1s, -webkit-transform 0.25s ease-out 0.1s;transition:transform 0.25s ease-out 0.1s,-webkit-transform 0.25s ease-out 0.1s;width:1.5rem;}
.button-switch .switch:checked:after{-webkit-transform:translate(2.5rem,0);-ms-transform:translate(2.5rem,0);transform:translate(2.5rem,0);}
.button-switch .switch:checked ~ .lbl-off{opacity:0;}
.button-switch .switch:checked ~ .lbl-on{opacity:1;}
.button-switch .switch:checked:before{background:#00A651;}
.custom-form-check input[type="radio"]{display:none;}
.custom-form-check label{cursor:pointer;position:relative;padding-left:1.5rem;}
.custom-form-check label::before{content:"";position:absolute;width:1rem;height:1rem;background-color:transparent;border:2px solid #616265;border-radius:50%;top:50%;left:0;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%);-webkit-transition:border-color 400ms ease;-o-transition:border-color 400ms ease;transition:border-color 400ms ease;}
.custom-form-check label::after{content:"";position:absolute;width:0.7rem;height:0.7rem;background-color:var(--primary-color);border:2px solid var(--primary-color);border-radius:50%;top:50%;left:0.15rem;-webkit-transform:translateY(-50%) scale(0);-ms-transform:translateY(-50%) scale(0);transform:translateY(-50%) scale(0);-webkit-transition:-webkit-transform 400ms ease;transition:-webkit-transform 400ms ease;-o-transition:transform 400ms ease;transition:transform 400ms ease;transition:transform 400ms ease, -webkit-transform 400ms ease;}
.custom-form-check input[type="radio"]:checked + label::before{border-color:var(--primary-color);}
.custom-form-check input[type="radio"]:checked + label::after{-webkit-transform:translateY(-50%) scale(0.55);-ms-transform:translateY(-50%) scale(0.55);transform:translateY(-50%) scale(0.55);}

